<template>
  <div class="about">
    <div id="nav">
      <router-link to="/">TOP</router-link> |
      <router-link to="/about">LIST</router-link>
    </div>
    <h1 id="title">コロナ感染者数
      <br>
      全国一覧</h1>


    <ul>
      <li>
        <div class="day" v-if="datas"> {{ dateSlice(datas[0].last_updated.cases_date) }} 時点</div> 
      </li>
    </ul>


    <ul>
      <li class="prefectures" v-for="data in datas" :key="data.id">
        <div class="name text-3xl"> {{ data.name_ja }} </div> <br> <div class="data text-xl">累積感染者数 : {{ data.cases }} <br>累積死者数 : {{ data.deaths }} </div>
      </li>
    </ul>

  </div>
</template>

//---------------------------------------------------------------------------------

<script>
export default {
  computed: {
    datas() {
      return this.$store.state.datas
    }
  },
  created() {
    this.$store.dispatch('fetchDatas')
  },

  methods: {
      dateSlice(date) {
      const year = String(date).substring(0, 4)
      const month = String(date).substring(4, 6)
      const day = String(date).substring(6)
      return `${year}年${month}月${day}日`
    }

  }
}

</script>


